import OptionsWidget from "../options_widget.js";

const TPL = `
<div class="options-section">
    <h4></h4>

    <p>:</p>

    </div>
        <label><input type="checkbox" class="highlights-list-check" value="bold">  &nbsp;</label>
        <label><input type="checkbox" class="highlights-list-check" value="italic">  &nbsp;</label>
        <label><input type="checkbox" class="highlights-list-check" value="underline">  &nbsp;</label>
        <label><input type="checkbox" class="highlights-list-check" value="color">  &nbsp;</label>
        <label><input type="checkbox" class="highlights-list-check" value="bgColor">  &nbsp;</label>
    </div>

    <br/><br/>
    <h5></h5>

    <p> <code>#hideHighlightWidget</code> </p>
    
    <p>""->""()( "toggleRightPane")</p>
</div>`;

export default class HighlightsListOptions extends OptionsWidget {
    doRender() {
        this.$widget = $(TPL);
        this.$hlt = this.$widget.find("input.highlights-list-check");
        this.$hlt.on('change', () => {
            const hltVals = this.$widget.find('input.highlights-list-check[type="checkbox"]:checked').map(function () {
                return this.value;
            }).get();
            this.updateOption('highlightsList', JSON.stringify(hltVals));
        });
    }

    async optionsLoaded(options) {
        const hltVals = JSON.parse(options.highlightsList);
        this.$widget.find('input.highlights-list-check[type="checkbox"]').each(function () {
            if ($.inArray($(this).val(), hltVals) !== -1) {
                $(this).prop("checked", true);
            } else {
                $(this).prop("checked", false);
            }
        });
    }
}
